import { Checkbox, Input, Select } from '@chakra-ui/react';
import styled from '@emotion/styled';
import { Controller } from 'react-hook-form';

import { useOrderFormContext } from '../../../../../hooks/useOrderFormContext';
import { Spacing } from '../../../../common/layouts/Spacing';
import { LabelText } from '../Common/LabelText';

export const CashReceiptFields = () => {
  const { register, control, watch  } = useOrderFormContext();

  const hasCashReceipt = watch('hasCashReceipt');

  return (
    <Wrapper>
      <Controller
        control={control}
        name="hasCashReceipt"
        render={({ field: { onChange, value, ref } }) => (
          <Checkbox ref={ref} onChange={onChange} isChecked={value} colorScheme="yellow" size="lg" aria-label=" ">
            <LabelText> </LabelText>
          </Checkbox>
        )}
      />

      <Spacing />
      <Controller
        control={control}
        name="cashReceiptType"
        render={({ field }) => (
          <Select {...field} aria-label=" " isDisabled={!hasCashReceipt}>
            <option value="PERSONAL"></option>
            <option value="BUSINESS"></option>
          </Select>
        )}
      />
      <Spacing height={8} />
      <Input {...register('cashReceiptNumber')} placeholder="(-)  ." isDisabled={!hasCashReceipt}/>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  padding: 16px;
`;
